.card-container {
  display: flex;
  flex-direction: column;
  border: 1px solid grey;
  border-radius: 5px;
  background: #d44401;
  padding: 25px;
  cursor: pointer;
  transform: translateZ(0);
  transition: transform .25s ease-out;
}

.card-container:hover {
  transform: scale(1.05);
}